<template>
    <div style="margin-right:20px;margin-left:10px;">
        <div class="q-row">
            <span>问题：
            <div class="btn-group" style="background-color: white;float: right;margin-right: 5px;">
                            <button type="button" class="btn btn-Default" style="border-color: #ccc;background-color: white;height: 10px;line-height: 1px;font-size: 10px;" id="showSelectedIsAnswer">{{mData.isanswer}}</button>
                                      <button type="button" class="btn btn-Default dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false" style="border-color: #ccc;background-color: white;height: 10px;">
                                        <span class="caret" style="margin-top: -20px;"></span>
                                        <span class="sr-only">Toggle Dropdown</span>
                                      </button>
                                      <ul class="dropdown-menu" style="min-width: 80px;font-size: 10px;">
                                        <li><a @click="selectChangeIsAnswer(1)">必答</a></li>
                                        <li role="separator" class="divider" style="margin: 3px 0;"></li>
                                        <li><a @click="selectChangeIsAnswer(2)">非必答</a></li>
                                      </ul>
            </div>
            <textarea class="question" rows="2" v-model="mData.content" /></span>
            <span>备注<textarea class="question" rows="2" v-model="mData.ps" /></span>
            <hr>
            <div class="q-row">
                <span class="option-lb">最小字数.    </span>
                <input class="option" type="number" id="minnumber" name="minnumber" @blur="minCompaire" v-model="mData.minnumber" />
            </div>
            <div class="q-row">
                <span class="option-lb">最大字数.    </span>
                <input class="option" type="number" id="maxnumber" name="maxnumber" @blur="maxCompaire" v-model="mData.maxnumber" />
            </div>

        </div>
    </div>
</template>

<script>
    export default {
        name: "wenda",
        props: ["editData"],
        data() {
            return {
                mData: this.$props.editData
            }
        },
         methods: {
            selectChangeIsAnswer:function(obj){
                const temp = this.mData;
                if(obj==1){
                    temp.isanswer="必答";
                    $("#showSelectedIsAnswer").html("必答");
                }else{
                    temp.isanswer="非必答";
                    $("#showSelectedIsAnswer").html("非必答")
                }
            },
            minCompaire:function(){
                const temp = this.mData;
                if(Number(temp.minnumber)>=Number(temp.maxnumber)){
                    temp.minnumber=temp.maxnumber-1;
                    this.$set(this.mData,temp);
                    alert("最小数字应小于最大数字");
                }
            },
            maxCompaire:function(){
                const temp = this.mData;
                if(Number(temp.minnumber)>=Number(temp.maxnumber)){
                    temp.maxnumber=Number(temp.minnumber)+1;
                    this.$set(this.mData,temp);
                    alert("最大数字应大于最小数字");
                }
            },

         }
    }
</script>

